.text-center {
  text-align: center;
}

.bg-primario {
  background-color: #eea849;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #313131;
  font-size: 2rem;
}

.contents {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1,
h2,
h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3.2rem;
}

.nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}
@media (min-width: 1024px) {
  .nav {
    width: auto;
    flex-direction: row;
  }
}
.nav__link {
  font-size: 1.8rem;
  font-weight: 700;
  color: #313131;
}
.nav__link--login {
  background-color: #eea849;
  font-size: 2rem;
  padding: 1.2rem 3rem;
  border-radius: 1rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #ffffff;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
}
.nav__link--login:hover {
  cursor: pointer;
  background-color: #ea921a;
}
@media (min-width: 768px) {
  .nav__link--login {
    width: auto;
  }
}

.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 5rem 0;
}
@media (min-width: 1024px) {
  .header {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header__logo {
  width: 30rem;
  margin-bottom: 5rem;
}
@media (min-width: 1024px) {
  .header__logo {
    margin-bottom: 0;
  }
}

.home {
  padding: 15rem 0;
}
.home__flex {
  display: grid;
  gap: 0;
  grid-template-columns: repeat(1, 1fr);
  place-items: center;
}
@media (min-width: 1024px) {
  .home__flex {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.home__heading span {
  color: #eea849;
}
.home__img {
  display: none;
}
.home__img img {
  width: 80rem;
}
@media (min-width: 1024px) {
  .home__img {
    display: block;
  }
}
.home__content {
  width: 100%;
}

.steps__wave {
  margin-bottom: -1px;
}
.steps__content {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  background: #eea849;
}
.steps__content :is(h2, h4) {
  text-align: center;
  margin: 0;
}
.steps__content h2 {
  color: #ffffff;
}
.steps__content h4 {
  color: #ffffff;
  letter-spacing: 2px;
  font-weight: 400;
}
@media (min-width: 768px) {
  .steps__grid {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(3, 1fr);
    place-items: center;
  }
}
.steps__step {
  margin: 5rem 0;
}

.witnesses {
  padding: 5rem 0;
}
.witnesses__heading {
  text-align: center;
}
@media (min-width: 768px) {
  .witnesses__grid {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(5, 1fr);
  }
}

.witness {
  background-color: #eea849;
  padding: 2rem;
  margin-bottom: 2rem;
  border-radius: 1rem;
  color: #ffffff;
}
.witness:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .witness {
    margin-bottom: 0;
  }
  .witness:nth-child(1) {
    grid-column: 1/4;
  }
  .witness:nth-child(3) {
    grid-column: 5/6;
    grid-row: 1/3;
  }
  .witness:nth-child(4) {
    grid-column: 1/3;
  }
  .witness:nth-child(5) {
    grid-column: 3/5;
  }
}
.witness__header {
  display: flex;
  gap: 2rem;
  align-items: center;
}
.witness__img {
  border-radius: 50%;
  flex-basis: 10rem;
  overflow: hidden;
}
.witness__author {
  color: #ffffff;
  font-weight: bold;
  margin: 0.5rem 0;
}
.witness__text {
  font-size: 2rem;
  line-height: 1.5;
  margin: 2rem 0 0 0;
  padding-left: 4rem;
  position: relative;
}
.witness__text::before {
  content: '"';
  font-size: 10rem;
  position: absolute;
  left: 0;
  top: -4rem;
  opacity: 0.2;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande", "Lucida Sans", Arial, sans-serif;
}

.favorites {
  background-color: #eceeef;
  padding: 5rem;
}
.favorites__heading {
  text-align: center;
  color: #eea849;
}
@media (min-width: 768px) {
  .favorites__grid {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.favorite {
  background-color: #ffffff;
  padding: 4rem;
  margin-bottom: 2rem;
  border-radius: 1rem;
  box-shadow: 0 0 15px 2px rgba(0, 0, 0, 0.2);
}
.favorite:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .favorite:last-of-type {
    margin-bottom: 2rem;
  }
}
@media (min-width: 768px) {
  .favorite__grid {
    grid-template-columns: 2fr 3fr;
    gap: 2rem;
    align-items: center;
  }
}
@media (min-width: 1024px) {
  .favorite__grid {
    display: grid;
    grid-template-columns: 1fr 3fr;
  }
}
.favorite__img {
  max-width: 20rem;
  margin: 0 auto 2rem auto;
}
.favorite__content {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 1024px) {
  .favorite__content {
    align-items: flex-start;
  }
}
.favorite__name {
  margin: 1rem 0;
  color: #eea849;
  font-size: 2.4rem;
}
.favorite__description {
  margin: 0.5rem 0;
  text-align: center;
}
@media (min-width: 768px) {
  .favorite__description {
    text-align: left;
  }
}

.deliverys {
  padding: 5rem;
}
.deliverys__heading {
  text-align: center;
  color: #eea849;
}
@media (min-width: 1024px) {
  .deliverys__grid {
    display: grid;
    grid-template-columns: 40rem 1fr;
    gap: 2rem;
    place-items: center;
  }
}
.deliverys__content {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: flex-start;
}
.deliverys__text {
  line-height: 1.5;
}
.deliverys__info {
  background-color: #eea849;
  font-size: 2rem;
  padding: 1.2rem 3rem;
  border-radius: 1rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #ffffff;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
}
.deliverys__info:hover {
  cursor: pointer;
  background-color: #ea921a;
}
@media (min-width: 768px) {
  .deliverys__info {
    width: auto;
  }
}

.app {
  margin-bottom: 5rem;
}
.app__grid {
  background-color: #fae5c8;
  border-radius: 1rem;
}
@media (min-width: 1024px) {
  .app__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.app__content {
  padding: 5rem;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.app__text {
  text-transform: uppercase;
  color: #eea849;
}
.app__heading {
  color: #313131;
  margin: 0;
}
.app__description {
  color: #313131;
}
.app__btn--a {
  background-color: #eea849;
  font-size: 2rem;
  padding: 1.2rem 3rem;
  border-radius: 1rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #ffffff;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
}
.app__btn--a:hover {
  cursor: pointer;
  background-color: #ea921a;
}
@media (min-width: 768px) {
  .app__btn--a {
    width: auto;
  }
}